import React, { Component, Fragment } from 'react';
import Page from 'components/LayoutComponents/Page';
import BtnAddNew from 'components/common/button/BtnAddNew';
import BtnEdit from 'components/common/button/BtnEdit';
import BtnDelete from 'components/common/button/BtnDelete';
import { Card, Row, Table, Popconfirm, Col, message } from 'antd';
import { getListNation, deleteNation } from 'services/nation';

class Nationalitys extends Component {
  state = {
    dataTable: [],
    loading: false,
    pagination: {},
    page: 0,
    IsPagingEnabled: true,
  };

  componentDidMount() {
    this.getListNationTable();
  }

  handleTableChange = (pagination, filters, sorter) => {
    const { IsPagingEnabled } = this.state;
    this.setState({
      page: pagination.current,
    });
    this.getListNationTable(pagination.current, IsPagingEnabled);
  };

  getListNationTable = (page, IsPagingEnabled) => {
    this.setState({
      loading: true,
    });
    getListNation(page, IsPagingEnabled)
      .then(res => {
        const pagination = { ...this.state.pagination };
        pagination.total = res.data.totalResults;
        this.setState({
          dataTable: res.data.items,
          loading: false,
          pagination,
        });
      })
      .catch(error => {
        console.log('====>', error);
        this.setState({
          loading: false,
        });
      });
  };

  confirmDelete = e => {
    this.setState({
      loading: true,
    });
    deleteNation(e)
      .then(() => {
        message.success('Xóa quốc tịch thành công');
        this.getListNationTable();
        this.setState({
          loading: false,
        });
      })
      .catch(error => {
        this.setState({
          loading: false,
        });
        console.log('res', error);
        message.error(error.response.data.message);
      });
  };

  cancel = e => {};

  renderTable = () => {
    const { dataTable } = this.state;
    const columns = [
      {
        title: 'Mã quốc gia',
        dataIndex: 'id',
        key: 'id',
      },
      {
        title: 'Tên quốc gia',
        dataIndex: 'label',
        key: 'label',
      },
      {
        title: 'Thao tác',
        dataIndex: 'operation',
        render: (text, record) =>
          dataTable.length >= 1 ? (
            <Fragment>
              <BtnEdit href={`#nationalitys/form/edit/${record.id}`} />
              <Popconfirm
                title="Bạn có chăc chắn muốn xóa"
                onConfirm={() => this.confirmDelete(record.id)}
                onCancel={this.cancel}
                okText="Xóa"
                cancelText="Hủy"
              >
                <BtnDelete />
              </Popconfirm>
            </Fragment>
          ) : null,
      },
    ];
    return (
      <Table
        loading={this.state.loading}
        bordered
        dataSource={this.state.dataTable}
        columns={columns}
      />
    );
  };

  render() {
    return (
      <Card title="Danh mục quốc gia">
        <Row>
          <Col span={24}>
            <BtnAddNew href="#/nationalitys/form/new" />
          </Col>
        </Row>
        <Row>
          <Col span={24}>{this.renderTable()}</Col>
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <Nationalitys />
  </Page>
);



