import { request } from 'utils';

request.prefix = '/api/categories';

export const getListNation = () => request({
  url: '/Nation',
  method: 'GET',
})
export const getNation = (id) => request({
  url: `/Nation/${id}`,
  method: 'GET',
})
export const deleteNation = (id) => request({
  url: `/Nation/${id}`,
  method: 'DELETE',
})
export const updateNation = (id, values) => request({
  url: `/Nation/${id}`,
  data: {
    id: values.code,
    label: values.name,
    labelInEnglish: '',
  },
  method: 'PUT',
})
export const createNation = (values) => request({
  url: '/Nation',
  data: {
    id: values.code,
    label: values.name,
    labelInEnglish: '',
  },
  method: 'POST',
})


